import React, { useState } from 'react';
import { useEffect } from 'react';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import Header from './Header';
import { Doughnut } from 'react-chartjs-2';
import ChartDataLabels from 'chartjs-plugin-datalabels';
import deletar from '../images/deletar.svg';
import editar from '../images/editar.svg';
import novo from '../images/novo.svg';

function Gestor() {
  var html = 'https://pulsarapp-server.herokuapp.com';
  // var html = '//localhost:3001';
  var quotes = "'";

  const changeState = useDispatch();

  const loginLogin = useSelector((state) => state.userlogin);
  const loginUsuario = useSelector((state) => state.username);
  const gestor = useSelector((state) => state.gestor);

  const viewGestor = useSelector((state) => state.gestorselector);
  const [betterview, setBetterview] = useState(0);

  // dados do paciente.
  var box = useSelector((state) => state.box);
  var entrada = useSelector((state) => state.entrada);
  var nome = useSelector((state) => state.nome);
  var dn = useSelector((state) => state.dn);
  var hd = useSelector((state) => state.hd);
  var ap = useSelector((state) => state.ap);
  var alergias = useSelector((state) => state.alergias);
  var medprev = useSelector((state) => state.medprev);
  var exprev = useSelector((state) => state.exprev);
  var hda = useSelector((state) => state.hda);
  var status = useSelector((state) => state.status);
  var inter = useSelector((state) => state.inter);

  // invasões.
  var va = useSelector((state) => state.va);
  var dataVa = useSelector((state) => state.datava);
  var jid = useSelector((state) => state.jid);
  var dataJid = useSelector((state) => state.datajid);
  var jie = useSelector((state) => state.jie);
  var dataJie = useSelector((state) => state.datajie);
  var subcld = useSelector((state) => state.subcld);
  var dataSubcld = useSelector((state) => state.datasubcld);
  var subcle = useSelector((state) => state.subcle);
  var dataSubcle = useSelector((state) => state.datasubcle);
  var radd = useSelector((state) => state.radd);
  var dataRadd = useSelector((state) => state.dataradd);
  var rade = useSelector((state) => state.rade);
  var dataRade = useSelector((state) => state.datarade);
  var vfemd = useSelector((state) => state.vfemd);
  var dataVfemd = useSelector((state) => state.datavfemd);
  var vfeme = useSelector((state) => state.vfeme);
  var dataVfeme = useSelector((state) => state.datavfeme);
  var afemd = useSelector((state) => state.afemd);
  var dataAfemd = useSelector((state) => state.dataafemd);
  var afeme = useSelector((state) => state.afeme);
  var dataAfeme = useSelector((state) => state.dataafeme);
  var snc = useSelector((state) => state.snc);
  var dataSnc = useSelector((state) => state.datasnc);
  var torax = useSelector((state) => state.torax);
  var dataTorax = useSelector((state) => state.datatorax);
  var abd = useSelector((state) => state.abd);
  var dataAbd = useSelector((state) => state.dataabd);
  var svd = useSelector((state) => state.svd);
  var dataSvd = useSelector((state) => state.datasvd);

  // vm.
  var vm = useSelector((state) => state.vm);
  var pp = useSelector((state) => state.pp);
  var peep = useSelector((state) => state.peep);
  var fi = useSelector((state) => state.fi);

  // carregamento da lista de ctis nos quais o usuário trabalha.
  const [hospitais, setHospitais] = useState([]);
  const [ctis, setCtis] = useState([]);
  const [medicos, setMedicos] = useState([]);
  const [selectedhospital, setSelectedhospital] = useState('');
  const [selectedcti, setSelectedcti] = useState('SELECIONE UM CTI');
  const [selectedmedicos, setSelectedmedicos] = useState('');
  const [selectedmedicosoncti, setSelectedmedicosoncti] = useState('');
  useEffect(() => {
    if (viewGestor === 1) {
      setBetterview(0);
      console.log('USER: ' + loginLogin);
      // carregando a lista de hospitais.
      axios.get(html + '/ghospitais/').then((response) => {
        setHospitais(response.data);
        // selecionando o primeiro hospital da lista.
        var x = response.data;
        const arrayHospitais = x.map((item) => item.hospital);
        setSelectedhospital(arrayHospitais[0]);
        loadMedicos(selectedhospital);
        loadCtis(selectedhospital);
        setTimeout(() => {
          setViewcticontrol(1);
        }, 2000);
        setTimeout(() => {
          setBetterview(1);
        }, 2500);
      });
      console.log('LISTA DE HOSPITAIS CARREGADA');
    }
    // eslint-disable-next-line
  }, [viewGestor]);
  // função que captura o nome do hospital selecionado e carrega a lista de ctis.
  const selectHospital = (item) => {
    setSelectedhospital(item.hospital);
    loadMedicos(item.hospital);
    loadCtis(item.hospital);
  };

  // função que carrega a lista de médicos, após a seleção de um hospital.
  const loadMedicos = (value) => {
    if (viewGestor === 1) {
      axios.get(html + "/doctors/'" + value + "'").then((response) => {
        setMedicos(response.data);
      });
    }
  };
  // função que captura o nome do médico selecionado.
  const selectMedico = (item) => {
    setSelectedmedicos(item.plantonista);
  };

  // função que carrega a lista de médicos no cti selecionado.
  const [medicoscti, setMedicoscti] = useState([]);
  const loadMedicosOnCti = (hospital, cti) => {
    if (viewGestor === 1) {
      axios
        .get(html + "/doctorscti/'" + hospital + "'/'" + cti + "'")
        .then((response) => {
          setMedicoscti(response.data);
        });
    }
  };
  // função que captura o nome do médico selecionado.
  const selectMedicoOnCti = (item) => {
    setSelectedmedicosoncti(item.nome);
  };

  // função que carrega a lista de CTIs.
  const loadCtis = (value) => {
    // value pega o valor do hospital selecionado.
    if (viewGestor === 1) {
      axios.get(html + "/gctis/'" + value + "'").then((response) => {
        setCtis(response.data);
      });
    }
  };
  // função que captura o nome do CTI selecionado e carrega a lista de pacientes.
  const selectCti = (item) => {
    setViewcticontrol(0);
    setSelectedcti(item.cti);
    setTimeout(() => {
      loadPacientes(item.cti);
      loadMedicosOnCti(selectedhospital, selectedcti);
    }, 1000);
    setTimeout(() => {
      setViewcticontrol(1);
    }, 1500);
  };

  // função que carrega os pacientes estratificados por risco, após a seleção de um CTI.
  const [pacientes, setPacientes] = useState([]);
  const [pacientesEstavel, setPacientesEstavel] = useState([]);
  const [pacientesAlerta, setPacientesAlerta] = useState([]);
  const [pacientesInstavel, setPacientesInstavel] = useState([]);
  const loadPacientes = (value) => {
    if (viewGestor === 1) {
      axios
        .get(html + '/pacientes/' + quotes + value + quotes)
        .then((response) => {
          setPacientes(response.data);
        });
      axios
        .get(html + '/pacientesinstavel/' + quotes + value + quotes)
        .then((response) => {
          setPacientesInstavel(response.data);
        });
      axios
        .get(html + '/pacientesalerta/' + quotes + value + quotes)
        .then((response) => {
          setPacientesAlerta(response.data);
        });
      axios
        .get(html + '/pacientesestavel/' + quotes + value + quotes)
        .then((response) => {
          setPacientesEstavel(response.data);
        });
    }
  };

  // função que seleciona um paciente e abre a tela corrida.
  const selectItem = (item) => {
    var box = item.box;
    var entrada = item.entrada;
    var nome = item.nome;
    var dn = item.dn;
    var hd = item.hd;
    var ap = item.ap;
    var alergias = item.alergias;
    var medprev = item.medprev;
    var exprev = item.exprev;
    var hda = item.hda;
    var status = item.status;
    var inter = item.inter;
    var va = item.va;
    var dataVa = item.datava;
    var jid = item.jid;
    var dataJid = item.datajid;
    var jie = item.jie;
    var dataJie = item.datajie;
    var subcld = item.subcld;
    var dataSubcld = item.datasubcld;
    var subcle = item.subcle;
    var dataSubcle = item.datasubcle;
    var radd = item.radd;
    var dataRadd = item.dataradd;
    var rade = item.rade;
    var dataRade = item.datarade;
    var vfemd = item.vfemd;
    var dataVfemd = item.datavfemd;
    var vfeme = item.vfeme;
    var dataVfeme = item.datavfeme;
    var afemd = item.afemd;
    var dataAfemd = item.dataafemd;
    var afeme = item.afeme;
    var dataAfeme = item.dataafeme;
    var svd = item.svd;
    var dataSvd = item.datasvd;
    var torax = item.torax;
    var dataTorax = item.datatorax;
    var abd = item.abd;
    var dataAbd = item.dataabd;
    var snc = item.snc;
    var dataSnc = item.datasnc;
    var vm = item.vm;
    var pp = item.pp;
    var peep = item.peep;
    var fi = item.fi;

    changeState({
      type: 'CORRIDA_ON',
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadHospital: selectedhospital,
      payloadCti: selectedcti,
      payloadGestor: gestor,
      payloadIdpct: item.id,
      payloadBox: box,
      payloadEntrada: entrada,
      payloadNome: nome,
      payloadDn: dn,
      payloadHd: hd,
      payloadAp: ap,
      payloadAlergias: alergias,
      payloadMedprev: medprev,
      payloadExprev: exprev,
      payloadHda: hda,
      payloadStatus: status,
      payloadVa: va,
      payloadDatava: dataVa,
      payloadJid: jid,
      payloadDatajid: dataJid,
      payloadJie: jie,
      payloadDatajie: dataJie,
      payloadSubcld: subcld,
      payloadDatasubcld: dataSubcld,
      payloadSubcle: subcle,
      payloadDatasubcle: dataSubcle,
      payloadRadd: radd,
      payloadDataradd: dataRadd,
      payloadRade: rade,
      payloadDatarade: dataRade,
      payloadVfemd: vfemd,
      payloadDatavfemd: dataVfemd,
      payloadVfeme: vfeme,
      payloadDatavfeme: dataVfeme,
      payloadAfemd: afemd,
      payloadDataafemd: dataAfemd,
      payloadAfeme: afeme,
      payloadDataafeme: dataAfeme,
      payloadSvd: svd,
      payloadDatasvd: dataSvd,
      payloadSnc: snc,
      payloadDatasnc: dataSnc,
      payloadTorax: torax,
      payloadDatatorax: dataTorax,
      payloadAbd: abd,
      payloadDataabd: dataAbd,
      payloadVm: vm,
      payloadPp: pp,
      payloadPeep: peep,
      payloadFi: fi,
      payloadInter: inter,
    });
  };

  // função que adiciona um paciente.
  const insertPaciente = () => {
    changeState({
      type: 'INSERT_PACIENTE',
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadHospital: selectedhospital,
      payloadCti: selectedcti,
      payloadGestor: gestor,
    });
  };

  // função que atualiza um paciente.
  const updateItem = (item) => {
    changeState({
      type: 'UPDATE_PACIENTE',
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadHospital: selectedhospital,
      payloadCti: selectedcti,
      payloadGestor: gestor,
      payloadIdpct: item.id,
      payloadBox: item.box,
      payloadEntrada: item.entrada,
      payloadNome: item.nome,
      payloadDn: item.dn,
      payloadHd: item.hd,
      payloadAp: item.ap,
      payloadAlergias: item.alergias,
      payloadMedprev: item.medprev,
      payloadExprev: item.exprev,
      payloadHda: item.hda,
      payloadStatus: item.status,
      payloadVa: item.va,
      payloadDatava: item.datava,
      payloadJid: item.jid,
      payloadDatajid: item.datajid,
      payloadJie: item.jie,
      payloadDatajie: item.datajie,
      payloadSubcld: item.subcld,
      payloadDatasubcld: item.datasubcld,
      payloadSubcle: item.subcle,
      payloadDatasubcle: item.datasubcle,
      payloadRadd: item.radd,
      payloadDataradd: item.dataradd,
      payloadRade: item.rade,
      payloadDatarade: item.datarade,
      payloadVfemd: item.vfemd,
      payloadDatavfemd: item.datavfemd,
      payloadVfeme: item.vfeme,
      payloadDatavfeme: item.datavfeme,
      payloadAfemd: item.afemd,
      payloadDataafemd: item.dataafemd,
      payloadAfeme: item.afeme,
      payloadDataafeme: item.dataafeme,
      payloadSvd: item.svd,
      payloadDatasvd: item.datasvd,
      payloadSnc: item.snc,
      payloadDatasnc: item.datasnc,
      payloadTorax: item.torax,
      payloadDatatorax: item.datatorax,
      payloadAbd: item.abd,
      payloadDataabd: item.dataabd,
      payloadVm: item.vm,
      payloadPp: item.pp,
      payloadPeep: item.peep,
      payloadFi: item.fi,
      payloadInter: item.inter,
    });
  };

  // legenda para o gráfico.
  function Legenda() {
    return (
      <div
        className="secondary"
        style={{ flexDirection: 'row', marginTop: 20, marginBottom: 20 }}
      >
        <div
          id="ESTÁVEIS"
          style={{
            width: 20,
            height: 20,
            borderRadius: 5,
            backgroundColor: '#52be80',
            margin: 2.5,
            padding: 0,
          }}
        ></div>
        <p
          className="title2"
          style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
        >
          ESTÁVEIS
        </p>
        <div
          id="ALERTA"
          style={{
            width: 20,
            height: 20,
            borderRadius: 5,
            backgroundColor: '#f7dc6f',
            margin: 2.5,
            padding: 0,
          }}
        ></div>
        <p
          className="title2"
          style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
        >
          ALERTA
        </p>
        <div
          id="INSTÁVEIS"
          style={{
            width: 20,
            height: 20,
            borderRadius: 5,
            backgroundColor: '#ec7063',
            margin: 2.5,
            padding: 0,
          }}
        ></div>
        <p
          className="title2"
          style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
        >
          INSTÁVEIS
        </p>
        <div
          id="VAGOS"
          style={{
            width: 20,
            height: 20,
            borderRadius: 5,
            backgroundColor: 'rgba(153,163,164,1)',
            margin: 2.5,
            padding: 0,
          }}
        ></div>
        <p
          className="title2"
          style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
        >
          VAGOS
        </p>
      </div>
    );
  }

  // MODAL PARA EXCLUSÃO DE PLANTONISTA OU PACIENTE.
  const [modalTrigger, setModalTrigger] = useState(0);
  const [modalMessage, setModalMessage] = useState('');
  const [modalType, setModalType] = useState(0);
  const [modalId, setModalId] = useState(0); // id do registro selecionado na lista.

  // função para confirmar exclusão do paciente.
  const clickDeletePaciente = (id) => {
    setModalTrigger(1);
    setModalType(1);
    setModalMessage('CONFIRMAR EXCLUSÃO DO PACIENTE?');
    setModalId(id);
  };
  const deletePaciente = () => {
    if (modalType === 1) {
      // deletando primeiro os registros referentes ao paciente a ser excluído:
      fetch(html + '/deletetudo/' + modalId);
      // deletando o registro do paciente.
      fetch(html + '/deletepaciente/' + modalId);
      console.log('EXCLUINDO PACIENTE E TODOS OS SEUS REGISTROS: ' + modalId);
      setModalTrigger(0);
      toastSuccess();
    } else {
      return null;
    }
  };

  // função para confirmar exclusão do plantonista em um CTI.
  const clickDeletePlantonistaCti = (item) => {
    //ANIMAÇÃO DO GRÁFICO
    setModalTrigger(2);
    setModalType(1);
    setModalMessage(
      'CONFIRMAR EXCLUSÃO DO PLANTONISTA ' +
        item +
        'PARA O CTI ' +
        selectedcti +
        '?'
    );
  };
  const deletePlantonistaCti = () => {
    if (modalType === 1) {
      fetch(
        html +
          "/deletedoctorhospital/'" +
          selectedmedicosoncti +
          "'/'" +
          selectedhospital +
          "'"
      );
      setModalTrigger(0);
      toastSuccess();
    } else {
      return null;
    }
  };

  // função que fecha o modal.
  const holdData = () => {
    setModalTrigger(0);
  };

  // renderização do modal para exclusão do paciente.
  function ModalPaciente() {
    if (modalTrigger === 1) {
      return (
        <div
          className="secondary"
          style={{
            backgroundColor: 'rgba(0, 0, 0, 0.6)',
            borderRadius: 0,
            position: 'absolute',
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            height: '100%',
            width: '100%',
            marginTop: 0,
            marginBottom: 0,
            marginLeft: 0,
            marginRight: 0,
            zIndex: 3,
          }}
        >
          <div
            className="secondary"
            style={{
              backgroundColor: '#FFFFFF',
              borderRadius: 5,
              paddingTop: 50,
              paddingBottom: 50,
              paddingLeft: 50,
              paddingRight: 50,
            }}
          >
            <label
              style={{
                marginTop: 5,
                marginBottom: 5,
                marginLeft: 5,
                marginRight: 5,
                color: '#2C3E50',
                fontSize: 16,
                fontWeight: 'bold',
                textAlign: 'center',
              }}
            >
              {modalMessage}
            </label>
            <div
              style={{
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'flex-start',
              }}
            >
              <button
                className="green-button"
                style={{ width: 100 }}
                onClick={() => deletePaciente()}
              >
                EXCLUIR
              </button>
              <button
                className="red-button"
                style={{ width: 100 }}
                onClick={() => holdData()}
              >
                CANCELAR
              </button>
            </div>
          </div>
        </div>
      );
    } else {
      return null;
    }
  }

  // renderização do modal para exclusão do plantonista em um CTI.
  function ModalPlantonistaCti() {
    if (modalTrigger === 2) {
      return (
        <div
          className="secondary"
          style={{
            backgroundColor: 'rgba(0, 0, 0, 0.6)',
            borderRadius: 0,
            position: 'absolute',
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            height: '100%',
            width: '100%',
            marginTop: 0,
            marginBottom: 0,
            marginLeft: 0,
            marginRight: 0,
            zIndex: 3,
          }}
        >
          <div
            className="secondary"
            style={{
              backgroundColor: '#FFFFFF',
              borderRadius: 5,
              paddingTop: 50,
              paddingBottom: 50,
              paddingLeft: 50,
              paddingRight: 50,
            }}
          >
            <label
              style={{
                marginTop: 5,
                marginBottom: 5,
                marginLeft: 5,
                marginRight: 5,
                color: '#2C3E50',
                fontSize: 16,
                fontWeight: 'bold',
                textAlign: 'center',
              }}
            >
              {modalMessage}
            </label>
            <div
              style={{
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'flex-start',
              }}
            >
              <button
                className="green-button"
                style={{ width: 100 }}
                onClick={() => deletePlantonistaCti()}
              >
                EXCLUIR
              </button>
              <button
                className="red-button"
                style={{ width: 100 }}
                onClick={() => holdData()}
              >
                CANCELAR
              </button>
            </div>
          </div>
        </div>
      );
    } else {
      return null;
    }
  }

  // TOAST.
  var toastColor = '';
  var toastMessage = '';
  const toastSuccess = () => {
    changeState({
      type: 'GESTOR_ON',
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadHospital: selectedhospital,
      payloadCti: selectedcti,
      payloadGestor: gestor,
    });
    loadPacientes(selectedcti);
    toastColor = '#52BE80';
    toastMessage = 'PACIENTE EXCLUÍDO COM SUCESSO';
    changeState({
      type: 'TOAST_SUCCESS_PACIENTES',
      payloadToastColor: toastColor,
      payloadToastMessage: toastMessage,
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadHospital: selectedhospital,
      payloadCti: selectedcti,
      payloadGestor: gestor,
      payloadBox: box,
      payloadEntrada: entrada,
      payloadNome: nome,
      payloadDn: dn,
      payloadHd: hd,
      payloadAp: ap,
      payloadAlergias: alergias,
      payloadMedprev: medprev,
      payloadExprev: exprev,
      payloadHda: hda,
      payloadStatus: status,
      payloadVa: va,
      payloadDatava: dataVa,
      payloadJid: jid,
      payloadDatajid: dataJid,
      payloadJie: jie,
      payloadDatajie: dataJie,
      payloadSubcld: subcld,
      payloadDatasubcld: dataSubcld,
      payloadSubcle: subcle,
      payloadDatasubcle: dataSubcle,
      payloadRadd: radd,
      payloadDataradd: dataRadd,
      payloadRade: rade,
      payloadDatarade: dataRade,
      payloadVfemd: vfemd,
      payloadDatavfemd: dataVfemd,
      payloadVfeme: vfeme,
      payloadDatavfeme: dataVfeme,
      payloadAfemd: afemd,
      payloadDataafemd: dataAfemd,
      payloadAfeme: afeme,
      payloadDataafeme: dataAfeme,
      payloadSvd: svd,
      payloadDatasvd: dataSvd,
      payloadSnc: snc,
      payloadDatasnc: dataSnc,
      payloadTorax: torax,
      payloadDatatorax: dataTorax,
      payloadAbd: abd,
      payloadDataabd: dataAbd,
      payloadVm: vm,
      payloadPp: pp,
      payloadPeep: peep,
      payloadFi: fi,
      payloadInter: inter,
    });
    setTimeout(() => {
      changeState({
        type: 'GESTOR_ON',
        payloadUserLogin: loginLogin,
        payloadUserName: loginUsuario,
        payloadHospital: selectedhospital,
        payloadCti: selectedcti,
        payloadGestor: gestor,
      });
    }, 3500);
  };

  // renderização da lista de hospitais.
  function ShowHospitais() {
    return (
      <div>
        <p className="title2" style={{ fontSize: 18 }}>
          MEUS HOSPITAIS
        </p>
        <div
          className="scrollpacientes"
          id="LISTA DE HOSPITAIS"
          style={{
            justifyContent: 'flex-start',
            borderRadius: 5,
            margin: 10,
            marginTop: 12.5,
            margnLeft: 5,
            marginRight: 5,
            height: 400,
            width: 200,
          }}
        >
          {hospitais.map((item) => (
            <p
              key={item.id}
              className="row"
              style={{
                position: 'relative',
                margin: 0,
              }}
            >
              <button
                className="widget"
                style={{
                  width: 155,
                  color: '#ffffff',
                  backgroundColor:
                    item.hospital === selectedhospital ? '#ec7063' : '#1f7a8c',
                }}
                title="CTI"
                onClick={() => selectHospital(item)}
              >
                {item.hospital}
              </button>
            </p>
          ))}
        </div>
      </div>
    );
  }

  // renderização da lista de médicos vinculados ao hospital selecionado.
  function ShowMedicos() {
    if (medicos.length > 0) {
      return (
        <div>
          <p className="title2" style={{ fontSize: 18 }}>
            MEUS PLANTONISTAS
          </p>
          <div
            className="scrollpacientes"
            id="LISTA DE MÉDICOS"
            style={{
              justifyContent: 'flex-start',
              borderRadius: 5,
              margin: 2.5,
              height: 400,
              width: 300,
            }}
          >
            {medicos.map((item) => (
              <p
                key={item.id}
                className="row"
                style={{
                  position: 'relative',
                  margin: 0,
                  alignItems: 'center',
                  justifyContent: 'center',
                }}
              >
                <button
                  className="widget"
                  style={{
                    width: 210,
                    color: '#ffffff',
                    backgroundColor:
                      item.plantonista === selectedmedicos
                        ? '#ec7063'
                        : '#1f7a8c',
                  }}
                  title="CTI"
                  onClick={() => selectMedico(item)}
                >
                  {item.plantonista}
                </button>
                <button
                  className="delete-button"
                  title="EXCLUIR MÉDICO."
                  // onClick={() => clickDeletePlantonista(item.id)} BUCETA.
                  style={{ marginLeft: 0, height: 50, width: 50 }}
                >
                  <img
                    alt=""
                    src={deletar}
                    style={{
                      marginTop: 20,
                      marginBottom: 20,
                      marginLeft: 20,
                      marginRight: 20,
                      height: 30,
                      width: 30,
                    }}
                  ></img>
                </button>
              </p>
            ))}
          </div>
          <div
            className="secondary"
            style={{ flexDirection: 'row', justifyContent: 'flex-end' }}
          >
            <button
              className="blue-button"
              // onClick={() => insertDoctor()} // BUCETA
              title="ADICIONAR PLANTONISTA."
              style={{
                margin: 0,
                marginTop: 0,
                marginLeft: -5,
                height: 50,
                width: 50,
                paddingLeft: 10,
                paddingRight: 10,
              }}
            >
              <img
                alt=""
                src={novo}
                style={{
                  marginTop: 20,
                  marginBottom: 20,
                  marginLeft: 20,
                  marginRight: 20,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
          </div>
        </div>
      );
    } else {
      return (
        <div>
          <p className="title2" style={{ fontSize: 18 }}>
            MEUS PLANTONISTAS
          </p>
          <div
            className="scrollpacientes"
            style={{ height: 400, width: 300, margin: 2.5 }}
          >
            <p className="title2" style={{ fontSize: 14, alignSelf: 'center' }}>
              {'SELECIONE UM HOSPITAL'}
            </p>
          </div>
        </div>
      );
    }
  }

  // renderização da lista de ctis.
  function ShowCtis() {
    if (ctis.length > 0) {
      return (
        <div>
          <p className="title2" style={{ fontSize: 18 }}>
            MEUS CTIS
          </p>
          <div>
            <div
              className="scrollpacientes"
              id="LISTA DE CTIs"
              style={{
                justifyContent: 'flex-start',
                borderRadius: 5,
                margin: 2.5,
                height: 400,
                width: 750,
              }}
            >
              {ctis.map((item) => (
                <p
                  key={item.id}
                  className="row"
                  style={{
                    position: 'relative',
                    margin: 0,
                    flexDirection: 'row',
                    justifyContent: 'left',
                    height: 380,
                  }}
                >
                  <button
                    className="widget"
                    style={{
                      width: 150,
                      color: '#ffffff',
                      backgroundColor:
                        item.cti === selectedcti ? '#ec7063' : '#1f7a8c',
                    }}
                    title="CTI"
                    onClick={() => selectCti(item)}
                    onLoad={() => volbok(item.cti)}
                  >
                    <div style={{ flexDirection: 'row' }}>
                      {item.cti +
                        '     ' +
                        item.leitos +
                        '/' +
                        pacientes.length}
                    </div>
                  </button>
                  <div>
                    <div
                      className="secondary"
                      style={{
                        display: 'flex',
                        flexDirection: 'column',
                        justifyContent: 'center',
                        alignItems: 'center',
                        backgroundColor: '#f1f3f9',
                        borderColor: '#f1f3f9',
                        borderRadius: 5,
                        padding: 10,
                        margin: 2.5,
                        height: 400,
                        width: 600,
                      }}
                    >
                      <Doughnut
                        data={dataChart}
                        width={250}
                        height={250}
                        plugins={ChartDataLabels}
                        options={{
                          plugins: {
                            datalabels: {
                              display: function (context) {
                                return (
                                  context.dataset.data[context.dataIndex] !== 0
                                );
                              },
                              color: '#FFFFFF',
                              font: {
                                weight: 'bold',
                                size: 16,
                              },
                            },
                          },
                          tooltips: {
                            enabled: false,
                          },
                          elements: {
                            arc: {
                              borderColor: '#F1F3F9',
                              borderWidth: 5,
                            },
                          },
                          animation: {
                            duration: 0,
                          },
                          title: {
                            display: false,
                            text: 'STATUS DOS PACIENTES:',
                          },
                          legend: {
                            display: false,
                            position: 'bottom',
                          },
                          maintainAspectRatio: true,
                          responsive: false,
                        }}
                      ></Doughnut>
                      <Legenda></Legenda>
                    </div>
                  </div>
                </p>
              ))}
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <div>
          <p className="title2" style={{ fontSize: 18 }}>
            MEUS CTIS
          </p>
          <div
            className="scrollpacientes"
            style={{ height: 400, width: 750, margin: 2.5 }}
          >
            <p className="title2" style={{ fontSize: 14, alignSelf: 'center' }}>
              {'SELECIONE UM HOSPITAL'}
            </p>
          </div>
        </div>
      );
    }
  }

  function ShowMedicosOnCti() {
    if (medicoscti.length > 0) {
      return (
        <div>
          <p className="title2" style={{ fontSize: 18 }}>
            {'PLANTONISTAS ' + selectedcti}
          </p>
          <div
            className="scrollpacientes"
            id="LISTA DE MÉDICOS NO CTI"
            style={{
              justifyContent: 'flex-start',
              borderRadius: 5,
              margin: 0,
              height: 400,
              width: 600,
            }}
          >
            {medicoscti.map((item) => (
              <p
                key={item.id}
                className="row"
                style={{
                  position: 'relative',
                  margin: 0,
                  alignItems: 'center',
                  justifyContent: 'center',
                }}
              >
                <button
                  className="widget"
                  style={{
                    width: 130,
                    color: '#ffffff',
                    backgroundColor:
                      item.nome === selectedmedicosoncti
                        ? '#ec7063'
                        : '#1f7a8c',
                  }}
                  title="MÉDICO"
                >
                  {item.nome}
                </button>
                <button
                  className="delete-button"
                  title="EXCLUIR DESTE CTI."
                  onClick={() => clickDeletePlantonistaCti(item.nome)}
                  style={{ marginLeft: 0, height: 50, width: 50 }}
                >
                  <img
                    alt=""
                    src={deletar}
                    style={{
                      marginTop: 20,
                      marginBottom: 20,
                      marginLeft: 20,
                      marginRight: 20,
                      height: 30,
                      width: 30,
                    }}
                  ></img>
                </button>
              </p>
            ))}
          </div>
          <div
            className="secondary"
            style={{ flexDirection: 'row', justifyContent: 'flex-end' }}
          >
            <button
              className="blue-button"
              // onClick={() => insertDoctorCti()} // BUCETA.
              title="ADICIONAR PLANTONISTA AO CTI."
              style={{
                margin: 0,
                marginTop: 0,
                marginLeft: -5,
                height: 50,
                width: 50,
                paddingLeft: 10,
                paddingRight: 10,
              }}
            >
              <img
                alt=""
                src={novo}
                style={{
                  marginTop: 20,
                  marginBottom: 20,
                  marginLeft: 20,
                  marginRight: 20,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
          </div>
        </div>
      );
    } else {
      return (
        <div>
          <p className="title2" style={{ fontSize: 18 }}>
            {'PLANTONISTAS DO ' + selectedcti}
          </p>
          <div
            className="scrollpacientes"
            style={{ height: 400, width: 300, margin: 2.5 }}
          >
            <p className="title2" style={{ fontSize: 14, alignSelf: 'center' }}>
              {'SELECIONE UM HOSPITAL'}
            </p>
          </div>
        </div>
      );
    }
  }

  // GRÁFICO DE DISTRIBUIÇÃO DE PACIENTES POR STATUS.
  const dataChart = {
    labels: [' INSTÁVEIS', ' ALERTA', ' ESTÁVEIS', 'VAGOS'],
    datasets: [
      {
        data: vbk,
        backgroundColor: [
          '#F1948A',
          '#F7DC6F',
          '#7DCEA0',
          'rgba(153,163,164,1)',
        ],
        borderColor: '#F1F3F9',
        hoverBorderColor: ['#F1F3F9', '#F1F3F9', '#F1F3F9', '#F1F3F9'],
      },
    ],
  };

  function Legenda() {
    return (
      <div
        className="secondary"
        style={{ flexDirection: 'row', marginTop: 5, marginBottom: 20 }}
      >
        <div
          id="ESTÁVEIS"
          style={{
            width: 20,
            height: 20,
            borderRadius: 5,
            backgroundColor: '#52be80',
            margin: 2.5,
            padding: 0,
          }}
        ></div>
        <p
          className="title2"
          style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
        >
          ESTÁVEIS
        </p>
        <div
          id="ALERTA"
          style={{
            width: 20,
            height: 20,
            borderRadius: 5,
            backgroundColor: '#f7dc6f',
            margin: 2.5,
            padding: 0,
          }}
        ></div>
        <p
          className="title2"
          style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
        >
          ALERTA
        </p>
        <div
          id="INSTÁVEIS"
          style={{
            width: 20,
            height: 20,
            borderRadius: 5,
            backgroundColor: '#ec7063',
            margin: 2.5,
            padding: 0,
          }}
        ></div>
        <p
          className="title2"
          style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
        >
          INSTÁVEIS
        </p>
        <div
          id="VAGOS"
          style={{
            width: 20,
            height: 20,
            borderRadius: 5,
            backgroundColor: 'rgba(153,163,164,1)',
            margin: 2.5,
            padding: 0,
          }}
        ></div>
        <p
          className="title2"
          style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
        >
          VAGOS
        </p>
      </div>
    );
  }

  // tentativa de incluir gráficos com dados mapeados para cada registro de cti (fracasso).
  var vbk = [];
  //const [vbk, setVbk] = useState([]);
  //const [xg, setXg] = useState(0);
  //const [xy, setXy] = useState(0);
  //const [xr, setXr] = useState(0);
  var red = 0;
  var yellow = 0;
  var green = 0;
  function volbok(value) {
    axios
      .get(html + '/pacientesinstavel/' + quotes + value + quotes)
      .then((response) => {
        var x = [0, 1];
        x = response.data;
        setTimeout(() => {
          red = x.length;
          //setXr(red);
          console.log('VERMELHO:' + red);
        }, 2000);
      });
    axios
      .get(html + '/pacientesalerta/' + quotes + value + quotes)
      .then((response) => {
        var x = [0, 1];
        x = response.data;
        setTimeout(() => {
          yellow = x.length;
          //setXy(yellow);
          console.log('AMARELO:' + yellow);
        }, 2000);
      });
    axios
      .get(html + '/pacientesestavel/' + quotes + value + quotes)
      .then((response) => {
        var x = [0, 1];
        x = response.data;
        setTimeout(() => {
          green = x.length;
          //setXg(green);
          console.log('VERDE:' + green);
        }, 2000);
      });
    vbk = [red, yellow, green];
    // setVbk([red, yellow, green]);
  }

  // renderização da lista de pacientes.
  function ShowPacientes() {
    if (pacientes.length > 0) {
      return (
        <div>
          <p className="title2" style={{ fontSize: 18 }}>
            {'PACIENTES ' + selectedcti}
          </p>
          <div
            className="scrollpacientes"
            id="LISTA DE PACIENTES"
            style={{
              justifyContent: 'flex-start',
              borderRadius: 5,
              height: 400,
            }}
          >
            {pacientes.map((item) => (
              <p
                key={item.id}
                className="row"
                style={{
                  position: 'relative',
                  marginLeft: 0,
                  marginRight: 0,
                  marginTop: 0,
                  marginBottom: 0,
                  alignItems: 'center',
                }}
              >
                <button
                  className="widget"
                  style={{ minWidth: 50, marginRight: 5, color: '#ffffff' }}
                  title="BOX"
                  disabled="true"
                >
                  {item.box}
                </button>
                <button
                  className="status"
                  data-status={item.status}
                  onClick={() => selectItem(item)}
                  title={
                    'STATUS: ' +
                    item.status +
                    '. CLIQUE PARA EVOLUIR. \nINTERCONSULTAS: ' +
                    item.inter
                  }
                  style={{
                    marginLeft: 0,
                    marginRight: 0,
                    width: 450,
                  }}
                >
                  {item.nome}
                </button>
                <div style={{ display: 'flex', flexDirection: 'row' }}>
                  <button
                    className="yellow-button"
                    title="EDITAR PACIENTE."
                    onClick={() => updateItem(item)}
                    style={{ height: 50, width: 50 }}
                  >
                    <img
                      alt=""
                      src={editar}
                      style={{
                        marginTop: 20,
                        marginBottom: 20,
                        marginLeft: 20,
                        marginRight: 20,
                        height: 30,
                        width: 30,
                      }}
                    ></img>
                  </button>
                  <button
                    className="delete-button"
                    title="EXCLUIR PACIENTE."
                    onClick={() => clickDeletePaciente(item.id)}
                    style={{ marginLeft: 0, height: 50, width: 50 }}
                  >
                    <img
                      alt=""
                      src={deletar}
                      style={{
                        marginTop: 20,
                        marginBottom: 20,
                        marginLeft: 20,
                        marginRight: 20,
                        height: 30,
                        width: 30,
                      }}
                    ></img>
                  </button>
                </div>
              </p>
            ))}
          </div>
          <NewPaciente></NewPaciente>
        </div>
      );
    } else {
      return (
        <p className="title2" style={{ width: 600 }}>
          NENHUM PACIENTE CADASTRADO
        </p>
      );
    }
  }

  function NewPaciente() {
    return (
      <div
        className="secondary"
        style={{ flexDirection: 'row', justifyContent: 'flex-end' }}
      >
        <button
          className="blue-button"
          onClick={() => insertPaciente()}
          title="ADICIONAR PACIENTE."
          style={{
            margin: 0,
            marginTop: 0,
            marginLeft: -5,
            height: 50,
            width: 50,
            paddingLeft: 10,
            paddingRight: 10,
          }}
        >
          <img
            alt=""
            src={novo}
            style={{
              marginTop: 20,
              marginBottom: 20,
              marginLeft: 20,
              marginRight: 20,
              height: 30,
              width: 30,
            }}
          ></img>
        </button>
      </div>
    );
  }

  const [viewcticontrol, setViewcticontrol] = useState(0);
  function CtiControl() {
    if (viewcticontrol === 1 && selectedcti != 'SELECIONE UM CTI') {
      return (
        <div
          id="CTI"
          className="secondary fade-in"
          style={{ flexDirection: 'row', justifyContent: 'space-between' }}
        >
          <ShowMedicosOnCti></ShowMedicosOnCti>
          <ShowPacientes></ShowPacientes>
        </div>
      );
    } else {
      return null;
    }
  }

  // renderização do componente.
  if (viewGestor === 1 && betterview === 1) {
    return (
      <div>
        <div
          className="main fade-in"
          style={{
            width: '100%',
            flexDirection: 'column',
          }}
        >
          <ModalPaciente></ModalPaciente>
          <ModalPlantonistaCti></ModalPlantonistaCti>
          <Header></Header>
          <div
            id="TOTAL"
            className="scrollpacientes"
            style={{
              height: 500,
              width: 1000,
              flexDirection: 'column',
              justifyContent: 'flex-start',
              backgroundColor: '#F8F9FC',
              borderColor: '#F8F9FC',
              margin: 2.5,
              overflowX: 'hidden',
            }}
          >
            <div
              id="PRINCIPAL"
              className="secondary fade-in"
              style={{
                flexDirection: 'row',
                justifyContent: 'center',
                width: 1000,
                margin: 5,
                padding: 10,
              }}
            >
              <ShowHospitais></ShowHospitais>
              <ShowCtis></ShowCtis>
            </div>
            <CtiControl></CtiControl>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}

export default Gestor;
